.buttons {
    margin-top: 4em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.pdf-content {
    margin-top: 15px;
}

.pdf {
    font-family: 'Product Sans Bold';
    font-size: 17px;
    color: white;
    background: rgb(56, 170, 56);
    border-radius: 1em;
    padding: 6px;
    cursor: pointer;
    display: inline-block;
    text-transform: uppercase;
    padding-left: 10px;
    letter-spacing: .8px;
}

.pdf img {
    width: 15px;
    height: 15px;
    margin-left: 5px;
}

.word {
    font-family: 'Product Sans Bold';
    font-size: 17px;
    color: white;
    background: rgb(69, 117, 255);
    border-radius: 1em;
    padding: 6px;
    margin-left: 10px;
    cursor: pointer;
    display: inline-block;
    padding-left: 10px;
    letter-spacing: .5px;
    text-transform: uppercase;
}

.word img {
    width: 15px;
    height: 15px;
    margin-left: 5px;
}


